<template lang="html">
  <div class="article">
    <h1 class="title">{{ title }}</h1>
    <i class="iconfont icon-calendar time">{{ time }}</i>
    <i class="iconfont icon-tags tags">
      <span class="label label-primary" v-for="(tag, index) in tags" v-bind:key="index" v-on:click.stop="goToTags(tag)">{{tag}}</span>
    </i>
    <div class="art-body" v-html="ArticleContent"></div>
    <div id="gitments"></div>
  </div>
</template>
<script>
  import { mapGetters, mapState } from 'vuex'
  import 'gitment/style/default.css'
  import Gitment from 'gitment'

  export default {
    name: 'blog-article',
    computed: {
      ...mapGetters(['ArticleContent']),
      ...mapState({
        title: state => state.article.curArticle.title,
        time: state => state.article.curArticle.time,
        tags: state => state.article.curArticle.tags
      })
    },
    methods: {
      getArticle ({ id }) {
        this.$store.dispatch('getSingleArticleById', { id })
      },
      goToTags (tag) {
        this.$router.push({ name: 'Tags', params: { tag } })
      }
    },
    destroyed () {
      this.$store.dispatch('clearArticle')
    },
    mounted () {
      this.getArticle({ id: this.$route.params.id })
      const gitment = new Gitment({
        id: window.location.pathname,
        owner: 'Arweil',
        repo: 'Arweil.github.io',
        oauth: {
          client_id: '29c6947e06cb7d20ecd1',
          client_secret: '7e303e4bfe123e41bca20ba3e178f7a7b663ca99'
        }
      })
      gitment.render('gitments')
    }
  }
</script>

<style lang="less">
  .art-body {
    margin-top: 60px;
    line-height: 28px;
    color: #5d686f;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 1px;
    h1, h2, h3, h4, h5, h6 {
      color: #4a4a4a;
    }
    blockquote {
      font-size: 16px;
    }
  }
</style>
<style lang="less" scoped>
  @media (max-width: 991px) {
    .article {
      margin: 70px 0 20px !important;
    }
  }

  .article {
    margin: 30px 60px;
  }

  .title {
    letter-spacing: -1px;
    line-height: 54px;
    margin: 0;
  }

  .time, .tags {
    display: block;
    color: #aaa;
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 10px;
    .label {
      cursor: pointer;
      margin-right: 10px;
    }
    &:before {
      margin-right: 10px;
    }
  }
</style>




